  position: absolute;
  z-index: 300;
  left: 0;
  bottom: 30px;
  width: 100%;
  text-align: center;
}

.flex-control-nav li {
  display: inline-block;
  margin: 0 8px;
}

.flex-control-nav li a {
  display: block;
  width: 10px;
  height: 10px;
  border-radius: 50%;
  border: 2px solid #333;
  background-color: #fff;
  font-size: 0;
  cursor: pointer;
  transition: none;
  -webkit-transition: none;
}

.flex-control-nav li a.flex-active {
  position: relative;
  top: -3px;
  left: -3px;
  right: -3px;
  width: 16px;
  height: 16px;
  background-color: #333;
  cursor: default;
}

